import { html, nothing } from 'lit';
import { OmniElement } from '../../omni-element.js';
import { debounce } from '../../omni-util.js';
import { property, query, state } from 'lit/decorators.js';

import {css} from 'lit';
const styles = css`omni-style,.omni .field,.control,input{position:relative;height:100%;display:flex;flex-direction:column}.icon{pointer-events:all !important;cursor:pointer !important;z-index:auto !important;display:flex;flex-direction:column;justify-content:center;align-items:center}.icon.is-left-icon{position:absolute;left:0;bottom:0;margin:auto 0}.icon.is-right-icon{position:absolute;right:0;bottom:0;margin:auto 0}.omni .input:focus-visible{box-shadow:none !important}omni-icon{font-size:1.5rem}omni-icon::part(icon){max-height:100%}.options{position:absolute;max-height:15rem;background:#fff;margin-top:5px;z-index:10;width:100%;border-radius:10px;box-shadow:0 8px 44px -5px rgba(38,55,79,.29);overflow:auto;cursor:pointer}`

import '../omni-options/omni-options.js';
import { type Option } from '../omni-dropdown/omni-dropdown.types.js';
import { styleMap } from 'lit/directives/style-map.js';

/**
 * To include within your project `import { OmniSearchElement } from 'omni-ui';`
 *
 * The Omni Search Element is a component in our design system, returns the characters entered by the user.
 * If the user pass the filtered options list then it will show an autocomplete list.
 * It is built with Lit and exposes several attributes for customization.
 *
 * ---
 *
 * ## 💡 Overview
 *
 * The `omni-search` Element component in our design system is designed to be flexible and customizable.
 * It exposes several attributes for customization, such as `ph`, `isOpen`,`inputDebounceTimer`
 * and `filteredOptions`
 * These attributes allow for versatile content customization per icon-action instance.
 *
 * ---
 *
 * ## 📝 Usage
 *
 * **Basic Omni Icon Action Rendering**:
 *
 * ```html
 * <omni-style>
 *   <omni-search @search-update = ${onFliter}
 *       inputDebounceTimer = 100
 *       ph = `Search`
 *       isopen
 *       .filteredOptions = [`abc`,`cde`]>
 *   </omni-search>
 * </omni-style>
 * ```
 * ** Call-back Function**:
 *
 * ```Js
 * filteredWords = filteredOptions;
 *
 * function onFliter(event){
 *   let value = event.detail.value;
 *   filteredWords = filteredOptions.filter((word) => {
 *      if( typeof word === 'object' &&
 *          word.value.toLowerCase().trim().includes(value.toLowerCase())){
 *          return word;
 *      }
 *      if( typeof word !== 'object' && word.toLowerCase().trim().includes(value.toLowerCase())){
 *         return word;
 *      }
 *   })
 * }
 * ```
 * @tagname omni-search
 * @attr {boolean} isOpen - Define the open state of omni-search element
 * @attr {string} ph - placeholder for omni-search element
 * @attr {string[]} filteredOptions - Array of numbers/strings/objects for search suggestions
 * @attr {number} inputDebounceTimer - debounce time for search
 * @event {CustomEvent<string>} search-update - fires event when user types in search box
 */

export class OmniSearchElement extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  /** @internal */
  @property({ type: Boolean }) isExpanded: boolean = false;
  /** @internal */
  @property({ type: String }) value: string = '';
  @property({ type: String }) ph: string = '';
  @property({ type: Array, reflect: true }) filteredOptions: Option[] = [];
  @property({ type: Boolean, attribute: true, reflect: true }) isOpen: boolean = false;
  @property({ type: Number }) inputDebounceTimer: number = 750;
  /** @internal */
  @state() private _showFilterOptions: boolean = false;
  /** @internal */
  @state() private _optionsStyle = { top: '25px' };
  /** @internal */
  @query('.field') private _inputField: HTMLDivElement;

  async connectedCallback() {
    super.connectedCallback();
    if (this.filteredOptions.length > 0) document.addEventListener('click', this.#handleOutsideClick);
    if (this.value) {
      await this.updateComplete;
      this.updateSearch();
    }
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    document.removeEventListener('click', this.#handleOutsideClick);
  }

  updated(changed: Map<string, unknown>) {
    if (!changed.has('_optionsStyle') && this.filteredOptions?.length && this._inputField) {
      const height = this._inputField.getBoundingClientRect().height;
      this._optionsStyle = { top: `${height}px` };
    }
  }

  getInput(): HTMLInputElement | undefined {
    return this.shadowRoot.querySelector('#search-input');
  }

  async toggleSearch(flag = !this.isExpanded) {
    this.isExpanded = this.isOpen ? this.isOpen : flag;
    if (flag || this.isOpen) {
      await this.updateComplete;
      this.getInput()?.focus();
    }
  }

  closeSearch() {
    this.getInput().value = '';
    this.value = '';
    this.toggleSearch(false);
    this.updateSearch();
  }

  #optionsOpen() {
    if (this.filteredOptions.length > 0) {
      this._showFilterOptions = true;
    }
  }

  /** @internal */
  #handleOutsideClick = (event: MouseEvent) => {
    if (!event.composedPath().includes(this._inputField)) {
      this._showFilterOptions = false;
    }
  };

  updateSearch() {
    const { value } = this.getInput() ?? { value: this.value };
    this.value = value;
    this.dispatchNewEvent('search-update', { detail: { value } });
    this._showFilterOptions = true;
  }

  /** @internal */
  get xIconTemplate() {
    if (this.isOpen && !this.value?.length) return nothing;
    return html`
      <span class="icon is-right-icon" part="x-icon-wrapper">
        <omni-icon part="x-icon" id="clear-search-icon" icon-id="omni:interactive:close" @click=${this.closeSearch}>
        </omni-icon>
      </span>
    `;
  }

  #selectFromFilteredList(e: CustomEvent<Option>) {
    const data = e.detail;
    this.value = String(typeof data === 'object' ? data.value : data);
    this.dispatchNewEvent('search-update', { detail: { value: this.value.trim() } });
    this._showFilterOptions = false;
  }

  #handleEnterOnItem(e: CustomEvent<{ eventKey: string; value: Option }>) {
    if (e.detail.eventKey === 'Enter') {
      const data = e.detail.value;
      this.value = String(typeof data === 'object' ? data.value : data);
      this.dispatchNewEvent('search-update', { detail: { value: this.value.trim() } });
      this._showFilterOptions = false;
    }
    if (e.detail.eventKey === 'Escape') {
      this._showFilterOptions = false;
    }
  }

  render() {
    return html`
      <omni-style>
        ${this.isExpanded || this.isOpen
          ? html`
              <div class="field">
                <div class="control has-icons-left has-icons-right">
                  <input
                    class="input is-small"
                    part="input"
                    type="text"
                    id="search-input"
                    tabindex="-1"
                    placeholder="${this.ph || 'Search Entry'}"
                    @focus=${this.#optionsOpen}
                    @input=${debounce(() => this.updateSearch(), this.inputDebounceTimer)}
                    .value=${this.value} />
                  <span class="icon is-left-icon" part="search-icon-opened-wrapper">
                    <omni-icon
                      part="search-icon-opened"
                      icon-id="omni:interactive:search"
                      @click=${this.toggleSearch}></omni-icon>
                  </span>
                  ${this.xIconTemplate}
                </div>
                ${this._showFilterOptions && this.filteredOptions?.length
                  ? html`
                      <div class="options" style=${styleMap(this._optionsStyle)}>
                        <omni-options
                          .options=${this.filteredOptions}
                          @selectChange=${e => {
                            this.#selectFromFilteredList(e);
                          }}
                          @enterOnItem=${e => {
                            this.#handleEnterOnItem(e);
                          }}></omni-options>
                      </div>
                    `
                  : nothing}
              </div>
            `
          : html`
              <div class="icon" part="search-icon-closed-wrapper">
                <omni-icon
                  part="search-icon-closed"
                  icon-id="omni:interactive:search"
                  @click=${this.toggleSearch}></omni-icon>
              </div>
            `}
      </omni-style>
    `;
  }
}

OmniElement.register('omni-search', OmniSearchElement);

declare global {
  interface HTMLElementTagNameMap {
    'omni-search': OmniSearchElement;
  }
}
